$메인칼라 : #ff0000;


@mixin 함수($배경색) {
  background-color: $배경색;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}
.my-alert {
  @include 함수(#ffe591)
}
